import React from "react";
import { Routes, Route } from "react-router-dom";
import Reservation from "./pages/Reservation";

function App() {
	return (
		// 기존에 Switch 는 Routes 로 변경됨
		// Route 의 component 옵션은 사라지고 element 로 변경되며 아래처럼 쓰임
		// Route 의 exact 옵션 사라짐
		<Routes>
			<Route path="/" element={<Reservation />} />
		</Routes>
	);
}

export default App;
